@import url(https://fonts.googleapis.com/css?family=Open+Sans:700&display=swap);
html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
}

body {
  font-family: "Open Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,
body,
#root,
.full-height {
  height: 100%;
}

.area {
  width: 100%;
  height: calc((100% - 54px) - 15px);
  border: 15px solid;
  border-image-source: linear-gradient(0deg, #c24c4c, #9b1f1f);
  border-image-slice: 1;
  position: relative;
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.5);
}
.area:after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: 0;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  background: #c24c4c;
  width: 300px;
  height: 40px;
  -webkit-clip-path: polygon(0% 100%, 40px 0%, calc(100% - 40px) 0%, 100% 100%);
          clip-path: polygon(0% 100%, 40px 0%, calc(100% - 40px) 0%, 100% 100%);
}

.area:before {
  content: "";
  position: absolute;
  left: 50%;
  bottom: -15px;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  height: 60px;
  width: 60px;
  background: url(data:image/png;base64,) center center no-repeat;
  -webkit-background-size: contain;
  z-index: 2;
}

.text {
  color: #fff;
  background: #9b1f1f;
  display: inline-block;
  padding: 10px 0 10px 10px;
  font-size: 24px;
  text-transform: uppercase;
  margin-bottom: 15px;
  position: relative;
  height: 54px;
}
.text:after {
  content: "";
  position: absolute;
  right: -54px;
  top: -54px;
  border-top: 54px solid transparent;
  border-right: 54px solid transparent;
  border-bottom: 54px solid #9b1f1f;
}

